* {
  padding: 0;
  margin: 0;
}

.formulario-admin {
  margin-top: 5rem;
  width: 100%;
}

.campo-admin {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
}
.campo-admin label {
  flex: 0 0 10rem;
  font-size: 25px;
  font-family: sans-serif;
  margin-left: 15px;
}
.campo-admin .fecha-input {
  flex: 1;
  border: none;
  padding: 1.25rem;
  border-radius: 1rem;
}

.nombre-admin {
  color: black;
  text-align: center;
  padding-top: 100px;
  font-family: sans-serif;
  user-select: none;
}

.citas {
  margin-top: 5rem;
  padding-left: 5em;
}